@import url('https://fonts.googleapis.com/css2?family=Kanit&display=swap');

* {
  font-family: 'Kanit';
  margin: 0;
  padding: 0;
}

.upload-button {
    font-size: 1rem;
    background-color: #006CFF;
    border-radius: 4px;
    font-size: 1rem;
    border-radius: 20px;
    padding: 1.5em 3em;
    margin: 3em 4em;
    color: white;
    border: none;
    color: white;
    border: none;
}

.modal-background {
  background-color: rgba(64, 64, 64, 0.6);
  width: 100%;
  height: 100%;
  position: fixed;
  overflow-y: auto;
  top: 0;
  left: 0;
}

.modal-container {
  background-color: #fefefd;
  border: 0;
  border-radius: 1rem;
  margin: 0.5em auto;
  padding: 0;
  width: 50%;
}

.modal-header {
  position: relative;
  text-align: center;
  padding-top: 2em;
}

.modal-header h3 {
  font-weight: 600;
  font-size: 1.5rem;
  line-height: 32px;
  color: #1D1D1C;
  margin-bottom: 0.5em;
}

.modal-header p {
  margin-bottom: 3.25em;
  font-weight: 300;
  font-size: 1rem;
  line-height: 24px;
  color: #454545;
}

.close img {
  top: 2.5rem;
  right: 6rem;
  position: absolute;
  cursor: pointer;
}

.modal-body-container {
  padding: 0 3.5em 1em;
}

.modal-body-container h3 {
  font-weight: 700;
  font-size: 1.25rem;
  line-height: 24px;
}

.modal-body {
  /* padding: 2rem; */
  margin: 0;
  /* padding: 1em; */
  margin-bottom: 1em;
}

.drag-and-drop {
  text-align: center;
  border: dashed 1px grey;
  border-radius: 1rem;
  padding-inline: 0;
  padding: 1em 0;
  margin-bottom: 2em;
  background-color: #FBF9F6;
}

.drag-and-drop .or {
  background-color: #FEFEFD;
  padding: 0 0.5em;
  margin: -17px auto 0; 
  width: fit-content; 
  display: block;
}

.drag-and-drop .small-text {
  font-weight: 300;
  font-size: 1rem;
  line-height: 24px;
  color: #6a6a6a;
}

.drag-and-drop > img {
  width: 20%;
  min-width: 110px;
  display: inline;
}

.or-wrapper {
  padding: 1em 12em;
  margin: .2em 0;
}

.primary-button {
  font-weight: 500;
  font-size: 1rem;
  line-height: 24px;
  text-align: center;
  background-color: #006CFF;
  border-radius: 4px;
  width: 154px;
  height: 56px;
  color: white;
  border: none;
  margin-top: 1.5em;
}


