.upload-progress {
  padding-inline: 0;
}

.upload-progress-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  /* border: 1px solid #e2e2e2;  */
  border-radius: 5px;
  margin-bottom: 10px;
  /* padding: 0 10px; */
}

.upload-icon {
  padding-left: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
  /* width: 20%; */
}

.upload-progress-status {
  width: 80%;
}

.files-toggle {
  display: flex;
  align-items: center;
  justify-content: center;
}

.section-p-0 {
